import { PrimaryButton,TextField,Stack,Text, Label,DefaultEffects,Nav } from '@fluentui/react';
import React,{useState } from 'react';

function Stock()
{
    const [state,setState] = useState({
      curAvgPrice:0,
      curCount: 0,
      curTotalPrice: 0,
      addAvgPrice:0,
      addCount: 0,
      addTotalPrice: 0,
      resultAvgPrice:0,
      resultCount: 0,
      resultTotalPrice: 0
    });
   
    function curCalcul(e) {
      const value = parseInt( e.target.value);
      let newState = {...state, [e.target.name]:value};
      newState.curTotalPrice = newState.curCount*newState.curAvgPrice;
      newState.addTotalPrice = newState.addCount*newState.addAvgPrice;
      newState.resultCount = newState.curCount + newState.addCount;
      newState.resultTotalPrice = newState.curTotalPrice+ newState.addTotalPrice;
      newState.resultAvgPrice =  newState.resultTotalPrice  / newState.resultCount ;
      setState(newState);
    }
    return(
 
    <Stack.Item  align='center' tokens={{ }} >
    <Stack tokens={{}}>
      <Text variant="superLarge" block style={{marginBottom:40}}> </Text>
      <Text variant="large" styles={{}}>
        
      </Text>
      <Text variant="small"  style={{color:'gray'}}>
      (   ,  .)
      </Text>
      <Stack horizontal tokens={{childrenGap: "10" }}  style={{marginTop:10}} wrap >
      <Stack.Item><TextField style={{}} name='curAvgPrice' onChange={function(e){ curCalcul(e); } } placeholder=' '  suffix=""/></Stack.Item>
      <Stack.Item><TextField name='curCount' onChange={function(e){ curCalcul(e); } } placeholder=' ' suffix="" /></Stack.Item>
      <Stack.Item><TextField defaultValue={0} value={`${state.curTotalPrice}`} suffix=""/></Stack.Item>
      </Stack>
      <Text variant="large" style={{marginTop:10}}>
       
      </Text>
      <Text variant="small"  style={{color:'gray'}}>
      (   ,  .)
      </Text>
      <Stack horizontal tokens={{childrenGap: "10" }}  style={{marginTop:10}}   wrap>
      <Stack.Item>
        <TextField name='addAvgPrice' onChange={function(e){ curCalcul(e); } } placeholder=''  suffix=""/>
        </Stack.Item>
          <Stack.Item>
        <TextField name='addCount' onChange={function(e){ curCalcul(e); } } placeholder='' suffix="" />
        </Stack.Item>
        <Stack.Item>
        <TextField defaultValue={0} value={`${state.addTotalPrice}`} suffix=""/>
        </Stack.Item>
      </Stack>
      <Text variant="large" style={{marginTop:10}}>
      
      </Text>
      <Text variant="small"  style={{color:'gray'}}>
      (  .)
      </Text>
      <Stack horizontal tokens={{childrenGap: "10" }}  style={{marginTop:10}} wrap >
          <Stack.Item>
          <TextField readOnly name='resultAvgPrice' value={`${state.resultAvgPrice}`} placeholder=''  suffix=""/>
          </Stack.Item>
          <Stack.Item>
          <TextField readOnly name='resultCount' value={`${state.resultCount}`} placeholder='' suffix="" />
          </Stack.Item>
          <Stack.Item>
          <TextField readOnly defaultValue={0} value={`${state.resultTotalPrice}`} suffix=""/>
          </Stack.Item>
      </Stack>
      </Stack>
      </Stack.Item>
    );
}
export default Stock;